.lrpanel {
    margin: 5% auto auto auto;
    width: 20%;
    background-color: var(--primary-color);
    border-radius: 16px;
    padding: 20px;
    box-shadow: 
    0px 0px 0px 2px var(--outline-color) inset, 
    0px 4px 0px 0px var(--secondary-color);

    h2 {
        font-size: 36px;
        margin: 0 0 10px 0;
    }

    // * p and a tag - switch between login and register forms *
    .switch-lr, .switch-lr-link {
        font-size: 13px;
        font-weight: 500;
    }
    .switch-lr-link {
        color: var(--accent-color);
    }

    // * forms input boxes - username, email(opt), password *
    .lr-input{
        width: 100%;
        padding: 15px 0px;
        margin: 10px 0 10px 0;
        font-weight: 200;
        font-size: 21px;
        border-radius: 16px;
        // ? make the password input box's hidden dots bigger, doesn't work on some browsers
        // -webkit-text-security: disc;
    }

    .in-icon {
        position: relative;
        left: 0px;
        top: -10px
    }

    input[type="submit"]{
        width: 100%;
        padding: 17px !important;
        margin: 30px 0 30px 0;
        font-weight: 800;
        font-size: 21px;
        background-color: var(--accent-color);
        border-radius: 16px;
        border: 0px;
        padding: 10px;
        box-shadow: 
        0px 0px 0px 2px var(--outline-color) inset, 
        0px 4px 0px 0px var(--secondary-color);
    }
      
    input[type="submit"]:active{
        box-shadow: 
        0px 0px 0px 6px var(--outline-color) inset, 
        0px 10px 0px 0px var(--secondary-color), 
    }
}